<template>
  <div>
    <div class="clearfix top-filter">
      <el-row class="clearfix">
        <div class="fl">
          <el-button @click="handleGoBack">返回</el-button>
        </div>
        <div class="fr">
          <el-button v-if="pageType !== 'detail'" :loading="completionLoading" :disabled="form.status === 2" type="success" icon="el-icon-check" @click="handleFinish">完成</el-button>
        </div>
      </el-row>
    </div>
    <el-form ref="form" inline :model="form" label-width="80px" label-position="left">
      <el-row>
        <el-col :span="6">
          <el-form-item label="退库单号" prop="billNumber">
            <el-input v-model="form.billNumber" disabled placeholder="请填写退库单号" style="width: 178px;" />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="退库日期" prop="date">
            <el-input v-model="form.date" disabled placeholder="请填写退库日期" style="width: 178px;" />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="出库单号">
            <el-input v-model="form.stockOutNumber" disabled placeholder="请填写出库单号" style="width: 178px;" />
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="是否检验" prop="inspect">
            <el-radio-group v-model="form.inspect" style="width: 178px;" class="my-inspect-sty" :disabled="pageType === 'detail'" @input="inspectChange">
              <el-radio-button :label="true">是</el-radio-button>
              <el-radio-button :label="false">否</el-radio-button>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="form.detailList"
        :column-list="columnList"
        :operation-width="120"
        :table-height-type="685"
        :table-column-sort="false"
      >
        <template slot-scope="scope">
          <el-button v-if="pageType === 'detail'" type="primary" @click="handleModify(scope.row)">详情</el-button>
          <!-- status为3表示已经检验通过进入入库流程，不能再进行编辑 -->
          <el-button v-else type="warning" :disabled="scope.row.status === 3" icon="el-icon-edit" @click="handleModify(scope.row)">编辑</el-button>
        </template>
      </table-com>
    </el-row>
    <edit-dialog v-model="dlgView" :dialog-type="pageType" :title="dialogTitle" :extra-info="{tkInfo, materialInfo}" @submit="getDetail" />
  </div>
</template>
<script>
import { finishApi, getReturnWarehouseDetailApi as getDetailApi, getEditApi as editApi } from '@/apis/modules/out-bound/stock-return'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './EditDialog'
import { requestWrapper } from '@/utils/utils'
export default {
  components: {
    EditDialog
  },
  mixins: [paginationPage],
  props: {
    // 退库单号
    billNumber: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      dlgView: false,
      completionLoading: false,
      columnList: [
        {
          module: 'word',
          prop: 'materialGroupCode',
          label: '物料组',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'word',
          prop: 'materialCode',
          label: '物料编号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null

        },
        {
          module: 'word',
          prop: 'materialName',
          label: '物料名称',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '评估类型',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'stockOutNum',
          label: '出库数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'returnNum',
          label: '退货数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      form: {
        billNumber: '', // 退库单号
        date: '', // 退库日期
        stockOutNumber: '', // 出库单号
        inspect: null, // 是否检验
        detailList: [], // 退库物料
        status: '' // 退库单状态
      },
      tkInfo: {},
      materialInfo: {}
    }
  },
  computed: {
    pageType() {
      return this.$route.name.replace('WmsOutBoundStock', '').toLowerCase() || 'add'
    },
    dialogTitle() {
      return this.pageType === 'detail' ? '退库内容详情' : '退库内容编辑'
    }
  },
  mounted() {
    this.billNumber && this.getDetail()
  },
  methods: {
    handleModify(item) {
      this.materialInfo = item
      this.dlgView = true
    },
    // Tip 完成
    async handleFinish() {
      this.completionLoading = true
      const res = await requestWrapper(finishApi({ billNumber: this.billNumber }))
      if (!res.success) {
        this.completionLoading = false
        return
      }
      // Tip 关闭页面
      this.completionLoading = false
      this.handleGoBack()
      this.$message({
        message: `操作成功`,
        type: 'success'
      })
    },
    // Tip 返回
    handleGoBack() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({ name: 'OutBoundStockReturn' })
    },
    // Tip 退库详情
    async getDetail() {
      const params = {
        billNumber: this.$route.params.billNumber
      }
      return new Promise((resolve, reject) => {
        getDetailApi(params).then(res => {
          const { code, data } = res
          if (!code) {
            if (!data) return
            this.tkInfo = { ...data }
            this.form.id = this.tkInfo.id
            this.form.billNumber = this.tkInfo.billNumber
            this.form.date = this.tkInfo.date
            this.form.stockOutNumber = this.tkInfo.stockOutNumber
            this.form.inspect = this.tkInfo.inspect
            this.form.detailList = this.tkInfo.detailList
            this.form.status = this.tkInfo.status
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 是否检验状态改变回调
    inspectChange() {
      editApi({ id: this.tkInfo.id, inspect: this.form.inspect })
        .then(res => {
          console.log(res)
          this.getDetail()
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style scoped lang="scss">
::v-deep .my-inspect-sty {
  .el-radio-button__orig-radio:disabled+.el-radio-button__inner {
    color: #606266;
  }
}
</style>
