<template>
  <el-drawer
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    direction="btt"
    size="50%"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-row v-if="dialogType !== 'detail'" style="text-align: right;">
      <el-button type="primary" icon="el-icon-plus" @click="visible = true">新增</el-button>
    </el-row>
    <el-row>
      <table-com
        ref="baseTableRef"
        :loading="false"
        :selection="false"
        :table-column-sort="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="170"
        :table-height-type="270"
        :show-operate="dialogType !== 'detail'"
      >
        <template slot-scope="scope">
          <el-button type="danger" @click="handleDelete(scope.row,scope.index)">删除</el-button>
        </template>
      </table-com>
    </el-row>
    <div class="drawer-footer">
      <el-button :loading="loading" type="primary" @click="handleConfirm">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
      <el-button @click="beforeClose">取 消</el-button>
    </div>
    <!-- 退货内容三级弹窗  -->
    <barcode-list
      v-model="visible"
      :extra-info="{id: extraInfo.materialInfo.stockOutDetailId,forBusiness: 'returnWarehouse', returnWarehouseNumber: extraInfo.tkInfo.billNumber}"
      @confirm="selectedBarcode"
    />
    <package-to-s-k-u
      v-model="skuVisible"
      :title="'供应商条码'"
      :is-show-print-btn="false"
      :extra-info="{columnList: columnList1 || [], tableData: dialogInfo1.subBarCodeList || []}"
    />
  </el-drawer>
</template>

<script>
import { getDetailApi, getMaterialAddApi as addApi } from '@/apis/modules/out-bound/stock-return'
import { dictCodeMixins } from '@/mixins/dictionary'
import paginationPage from '@/mixins/paginationPage'
import BarcodeList from '@/components/ModalSelect/stockReturn'
import PackageToSKU from '@/components/ModalSelect/PackageToSKU'

export default {
  components: {
    BarcodeList,
    PackageToSKU
  },
  mixins: [dictCodeMixins, paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    dialogType: {
      type: String,
      default: ''
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      tableData: [],
      loading: false,
      columnList: [
        {
          module: 'word',
          prop: 'currentPosition',
          label: '库位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'link',
          type: (item) => Array.isArray(item.subBarCodeList) && !!item.subBarCodeList.length ? 'primary' : '',
          prop: 'barcodeNumber',
          underline: false,
          minWidth: 150,
          label: '条码号',
          align: 'center',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          handleClick: (item) => {
            if (!item.subBarCodeList.length) return
            this.skuVisible = true
            this.dialogInfo1 = item
          }
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        }
      ],
      columnList1: [
        {
          module: 'word',
          prop: 'currentPosition',
          label: '库位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '条码号',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'word',
          prop: 'num',
          label: '数量',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '计量单位',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: null
        }
      ],
      visible: false,
      skuVisible: false,
      dialogInfo1: {}
    }
  },
  methods: {
    initSelect() {
      this.getDetail()
    },
    selectedBarcode(list) {
      if (list.length) {
        this.tableData = list
      } else {
        this.tableData = []
      }
    },
    handleDelete(item, i) {
      this.$confirm('此操作将永久删除选中信息, 是否继续?', '提示', {
        confirmButtonText: '确 定',
        cancelButtonText: '取 消',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        this.tableData.splice(i, 1)
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '消息',
          message: '已取消删除'
        })
      })
    },
    handleConfirm(formName) {
      // 入若当前页面为退库详情则直接关闭抽屉
      if (this.dialogType === 'detail') {
        this.beforeClose()
        return
      }
      if (!this.tableData.length) {
        this.$message({
          message: '请选择退库内容',
          type: 'warning'
        })
        return
      }
      this.loading = true
      addApi({
        detailList: this.tableData,
        returnWarehouseNumber: this.extraInfo.tkInfo.billNumber,
        returnWarehouseMaterialId: this.extraInfo.materialInfo.id,
        stockOutDetailId: this.extraInfo.materialInfo.stockOutDetailId
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$message({
            message: `${this.title}成功`,
            type: 'success'
          })
          this.$emit('submit')
          this.beforeClose()
        }
        this.loading = false
      }).catch(err => {
        console.log(err)
        this.loading = false
      })
    },
    getDetail(row) {
      return new Promise((resolve, reject) => {
        getDetailApi({ returnWarehouseMaterialId: this.extraInfo.materialInfo.id })
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.tableData = data || []
              resolve(res)
            }
          })
          .catch(err => {
            console.log(err)
            reject(err)
          })
      })
    },
    beforeClose(done) {
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep:focus {
  outline: 0;
}
.drawer-footer {
  margin-bottom: 15px;
  text-align: right;
}
</style>

